input {
    padding: 15px 25px;
    outline: none;
    font-size: 2rem;
    border: 1px solid #999;
    font-weight: 700;
    width: 320px;
    margin-left: 1rem;
}
select {
    font-size: 1rem;
    border: 1px solid #999;
    margin-left: 1rem;
    padding: 5px;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}