.title {
    font-size: 1.75rem;
    margin: 4rem 0;
    margin-left: 1rem;
    color: blueviolet;
}
.converter {
    width: 800px;
    margin: auto;
    margin-top: 2rem;
}
.converter-inputs {
    margin-top: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.convert-examples {
    margin-top: 4rem;
    display: flex;
}
.convert-list {
    list-style-type: none;
    width: 50%;
    margin-left: 1rem;
    padding: 1rem 0;
}
.convert-list li {
    padding-left: 1rem;
    margin: 0.25rem 0;
    font-size: 1.25rem;
    border-bottom: 1px solid lightgray;
    padding-bottom: 10px;
}
.convert-list li:nth-child(1) {
    margin: 0.5rem 0;
    
}.popular-title {
    font-size: 1.75rem;
    margin: 2rem 0;
    margin-left: 1rem;
    color: blueviolet;
}
.popular {
    display: flex;
}
.popular-list {
    box-sizing: border-box;
    width: 50%;
    padding: 1rem;
    list-style-type: none;
}
.popular-list li {
    box-sizing: border-box;
    width: 80%;
    margin: 15px 0;
    padding: 10px;
    border: 1px solid #999;
    border-radius: 3px;
    transition: .3s;
}
