.footer {
    padding: 3rem 0;
}
.footer-container {
    width: 1100px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.socials {
    width: 25%;
}
.social-list {
    list-style-type: none;
    display: flex;
    justify-content: space-between;
}
a {
    text-decoration: none;
    color: #000;
}
a:hover {
    text-decoration: underline;
}